import { Base, Type, markType } from "igniteui-core/type";
import { GridScrollEventArgsDetail } from "./GridScrollEventArgsDetail";

/**
 * @hidden 
 */
export class GridScrollEventArgs extends Base {
	static $t: Type = markType(GridScrollEventArgs, 'GridScrollEventArgs');
	b: GridScrollEventArgsDetail = null;
	static a(a: any): GridScrollEventArgs {
		return ((() => {
			let $ret = new GridScrollEventArgs();
			$ret.b = <GridScrollEventArgsDetail>a;
			return $ret;
		})());
	}
}


