import { Enum, ValueType, markEnum, Type } from "./type";

/**
 * An enumeration for the different transaction types.
 */
export enum TransactionType {
	/**
	 * An add transaction.
	 */
	Add = 0,
	/**
	 * An update transaction.
	 */
	Update = 1,
	/**
	 * A delete transaction.
	 */
	Delete = 2
}

/**
 * @hidden 
 */
export let TransactionType_$type = markEnum('TransactionType', 'Add,0|Update,1|Delete,2');


