import { GridVerticalAlignment, GridVerticalAlignment_$type } from "./GridVerticalAlignment";
import { IgcSize } from "./igc-size";
import { PositionSettings as PositionSettings_internal } from "./PositionSettings";
import { ensureEnum } from "igniteui-core/componentUtil";

export class IgcPositionSettings
{

protected createImplementation() : PositionSettings_internal
{
	return new PositionSettings_internal();
}
	protected _implementation: any;
	                            /**
	                             * @hidden 
	                             */
	public get i() : PositionSettings_internal {
		return this._implementation;
	}
	private onImplementationCreated() {
		
	}
	constructor() {
	this._implementation = this.createImplementation();
	(<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	}
	protected _provideImplementation(i: any) {
	    this._implementation = i;
	    (<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	}
	/**
	 * @deprecated in version 10.2.0. Set the target point/element in the overlay settings instead
	 * Attaching target for the component to show
	*/
	get target() : any {
		return this.i.j as any;
	}
	set target(v: any) {
	                                        this.i.j = v;
	}
	/**
	 * Direction in which the component should show
	*/
	get horizontalDirection() : GridHorizontalAlignment {
		return this.i.a;
	}
	set horizontalDirection(v: GridHorizontalAlignment) {
		this.i.a = ensureEnum<GridHorizontalAlignment>(GridHorizontalAlignment_$type, v);
	}
	/**
	 * Direction in which the component should show
	*/
	get verticalDirection() : GridVerticalAlignment {
		return this.i.c;
	}
	set verticalDirection(v: GridVerticalAlignment) {
		this.i.c = ensureEnum<GridVerticalAlignment>(GridVerticalAlignment_$type, v);
	}
	/**
	 * Target's starting point
	*/
	get horizontalStartPoint() : GridHorizontalAlignment {
		return this.i.b;
	}
	set horizontalStartPoint(v: GridHorizontalAlignment) {
		this.i.b = ensureEnum<GridHorizontalAlignment>(GridHorizontalAlignment_$type, v);
	}
	/**
	 * Target's starting point
	*/
	get verticalStartPoint() : GridVerticalAlignment {
		return this.i.d;
	}
	set verticalStartPoint(v: GridVerticalAlignment) {
		this.i.d = ensureEnum<GridVerticalAlignment>(GridVerticalAlignment_$type, v);
	}
	/**
	 * The size up to which element may shrink when shown in elastic position strategy
	*/
	get minSize() : IgcSize {
	                                        const r = this.i.e;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
	                                        if (!(r as any).externalObject) {
	                                            let e = new IgcSize();
	                                            if ((r as any).$type) {
	                                                (e as any)._implementation = r;
	                                            } else {
	                                                if ((e.i as any).setNativeElement) {
	                                                    (e.i as any).setNativeElement(r);
	                                                }
	                                            }
	                                            (r as any).externalObject = e;
	                                        }
		return (r as any).externalObject;
	}
	set minSize(v: IgcSize) {
		v == null ? this.i.e = null : this.i.e = v.i;
	}
	
	    public findByName(name: string): any {
	        
	    if ((this as any).findEphemera) {
	        if (name && name.indexOf("@@e:") == 0) {
	            return (this as any).findEphemera(name);
	        }
	    }
	
	if (this.minSize && (this.minSize as any).name && (this.minSize as any).name == name) { return this.minSize; }
	
	        return null;
	    }
	public setNativeElement(element: any) {
		this.i.setNativeElement(element);
	}
}
