import { Base, Type, markType } from "./type";
import { TransactionType } from "./TransactionType";

/**
 * @hidden 
 */
export class TransactionState extends Base {
	static $t: Type = markType(TransactionState, 'TransactionState');
	private _id: any = null;
	get id(): any {
		return this._id;
	}
	set id(a: any) {
		this._id = a;
	}
	private _transactionType: TransactionType = <TransactionType>0;
	get transactionType(): TransactionType {
		return this._transactionType;
	}
	set transactionType(a: TransactionType) {
		this._transactionType = a;
	}
	private _value: any = null;
	get value(): any {
		return this._value;
	}
	set value(a: any) {
		this._value = a;
	}
	private _version: any = null;
	get version(): any {
		return this._version;
	}
	set version(a: any) {
		this._version = a;
	}
	constructor(a: any, b: TransactionType, c: any) {
		super();
		this.id = a;
		this.transactionType = b;
		this.value = c;
	}
}


