import { Base, Type, markType } from "igniteui-core/type";
import { RowSelectionEventArgsDetail } from "./RowSelectionEventArgsDetail";

/**
 * @hidden 
 */
export class RowSelectionEventArgs extends Base {
	static $t: Type = markType(RowSelectionEventArgs, 'RowSelectionEventArgs');
	b: RowSelectionEventArgsDetail = null;
	static a(a: any): RowSelectionEventArgs {
		return ((() => {
			let $ret = new RowSelectionEventArgs();
			$ret.b = <RowSelectionEventArgsDetail>a;
			return $ret;
		})());
	}
}


