import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { FieldPipeArgs as FieldPipeArgs_internal } from "./FieldPipeArgs";

export class IgcFieldPipeArgs
{

protected createImplementation() : FieldPipeArgs_internal
{
	return new FieldPipeArgs_internal();
}
	protected _implementation: any;
	                            /**
	                             * @hidden 
	                             */
	public get i() : FieldPipeArgs_internal {
		return this._implementation;
	}
	private onImplementationCreated() {
		
	}
	constructor() {
	this._implementation = this.createImplementation();
	(<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	}
	protected _provideImplementation(i: any) {
	    this._implementation = i;
	    (<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	}
	/**
	 * The date/time components that a date column will display, using predefined options or a custom format string.
	*/
	get format() : string {
		return (this.i.i as string);
	}
	set format(v: string) {
		this.i.i = v;
	}
	/**
	 * A timezone offset (such as '+0430'), or a standard UTC/GMT or continental US timezone abbreviation.
	*/
	get timezone() : string {
		return (this.i.j as string);
	}
	set timezone(v: string) {
		this.i.j = v;
	}
	/**
	 * Decimal representation options, specified by a string in the following format:
	 * `{minIntegerDigits}`.`{minFractionDigits}`-`{maxFractionDigits}`.
	 * `minIntegerDigits`: The minimum number of integer digits before the decimal point. Default is 1.
	 * `minFractionDigits`: The minimum number of digits after the decimal point. Default is 0.
	 * `maxFractionDigits`: The maximum number of digits after the decimal point. Default is 3.
	*/
	get digitsInfo() : string {
		return (this.i.g as string);
	}
	set digitsInfo(v: string) {
		this.i.g = v;
	}
	/**
	 * The currency code of type string, default value undefined
	*/
	get currencyCode() : string {
		return (this.i.f as string);
	}
	set currencyCode(v: string) {
		this.i.f = v;
	}
	/**
	 * Allow us to display currency 'symbol' or 'code' or 'symbol-narrow' or our own string.
	 * The value is of type string. By default is set to 'symbol'
	*/
	get display() : string {
		return (this.i.h as string);
	}
	set display(v: string) {
		this.i.h = v;
	}
	/**
	 * The first week day to be displayed in calendar when filtering or editing a date column
	*/
	get weekStart() : number {
		return (this.i.c as number);
	}
	set weekStart(v: number) {
		this.i.c = +v;
	}
	
	    public findByName(name: string): any {
	        
	    if ((this as any).findEphemera) {
	        if (name && name.indexOf("@@e:") == 0) {
	            return (this as any).findEphemera(name);
	        }
	    }
	
	
	        return null;
	    }
	public setNativeElement(element: any) {
		this.i.setNativeElement(element);
	}
}
