import { Base, Type, markType } from "igniteui-core/type";
import { RowToggleEventArgsDetail } from "./RowToggleEventArgsDetail";

/**
 * @hidden 
 */
export class RowToggleEventArgs extends Base {
	static $t: Type = markType(RowToggleEventArgs, 'RowToggleEventArgs');
	b: RowToggleEventArgsDetail = null;
	static a(a: any): RowToggleEventArgs {
		return ((() => {
			let $ret = new RowToggleEventArgs();
			$ret.b = <RowToggleEventArgsDetail>a;
			return $ret;
		})());
	}
}


