import { Base, Type, markType } from "igniteui-core/type";
import { PageCancellableEventArgsDetail } from "./PageCancellableEventArgsDetail";

/**
 * @hidden 
 */
export class PageCancellableEventArgs extends Base {
	static $t: Type = markType(PageCancellableEventArgs, 'PageCancellableEventArgs');
	b: PageCancellableEventArgsDetail = null;
	static a(a: any): PageCancellableEventArgs {
		return ((() => {
			let $ret = new PageCancellableEventArgs();
			$ret.b = <PageCancellableEventArgsDetail>a;
			return $ret;
		})());
	}
}


