import { Base, Type, markType } from "igniteui-core/type";
import { SortingEventArgsDetail } from "./SortingEventArgsDetail";

/**
 * @hidden 
 */
export class SortingEventArgs extends Base {
	static $t: Type = markType(SortingEventArgs, 'SortingEventArgs');
	b: SortingEventArgsDetail = null;
	static a(a: any): SortingEventArgs {
		return ((() => {
			let $ret = new SortingEventArgs();
			$ret.b = <SortingEventArgsDetail>a;
			return $ret;
		})());
	}
}


