import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcCancelableEventArgsDetail } from "./igc-cancelable-event-args-detail";
import { SortingEventArgsDetail as SortingEventArgsDetail_internal } from "./SortingEventArgsDetail";
import { IgcSortingExpression } from "./igc-sorting-expression";
import { IgcGroupingExpression } from "./igc-grouping-expression";

export class IgcSortingEventArgsDetail extends IgcCancelableEventArgsDetail
{

protected createImplementation() : SortingEventArgsDetail_internal
{
	return new SortingEventArgsDetail_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : SortingEventArgsDetail_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	get sortingExpressions() : IgcSortingExpression[] {
		if (!this.i.h) { return undefined; }
	                                        let ret: IgcSortingExpression[] = [];
	                                        for (let i = 0; i < this.i.h.length; i++) {
	                                            let impl = this.i.h[i];
	                                            if (!(<any>impl).externalObject) {
	                                                let e = new IgcSortingExpression();
	                                                (<any>e)._implementation = impl;
	                                                (<any>impl).externalObject = e;
	                                            }
	                                            ret.push((<any>impl).externalObject);
	                                        }
	                                        return ret;
	}
	set sortingExpressions(v: IgcSortingExpression[]) {
	                                        let arr: any[] = [];
	                                        for (let i = 0; i < v.length; i++) {
	                                            arr.push(v[i].i);
	                                        }
	                                        this.i.h = arr;
	                                    }
	get groupingExpressions() : IgcGroupingExpression[] {
		if (!this.i.g) { return undefined; }
	                                        let ret: IgcGroupingExpression[] = [];
	                                        for (let i = 0; i < this.i.g.length; i++) {
	                                            let impl = this.i.g[i];
	                                            if (!(<any>impl).externalObject) {
	                                                let e = new IgcGroupingExpression();
	                                                (<any>e)._implementation = impl;
	                                                (<any>impl).externalObject = e;
	                                            }
	                                            ret.push((<any>impl).externalObject);
	                                        }
	                                        return ret;
	}
	set groupingExpressions(v: IgcGroupingExpression[]) {
	                                        let arr: any[] = [];
	                                        for (let i = 0; i < v.length; i++) {
	                                            arr.push(v[i].i);
	                                        }
	                                        this.i.g = arr;
	                                    }
}
