import { CancelableEventArgsDetail } from "./CancelableEventArgsDetail";
import { SortingExpression } from "./SortingExpression";
import { Base, Type, markType } from "igniteui-core/type";
import { WCNativeHelper } from "igniteui-webinputs/WCNativeHelper";
import { GroupingExpression } from "./GroupingExpression";

/**
 * @hidden 
 */
export class SortingEventArgsDetail extends CancelableEventArgsDetail {
	static $t: Type = markType(SortingEventArgsDetail, 'SortingEventArgsDetail', (<any>CancelableEventArgsDetail).$);
	get h(): SortingExpression[] {
		let ret_ = this.b.j("sortingExpressions", (a: any) => new SortingExpression());
		return <SortingExpression[]>ret_;
	}
	set h(a: SortingExpression[]) {
		let value_ = <any>a;
		this.b.s("sortingExpressions", value_);
	}
	get g(): GroupingExpression[] {
		let ret_ = this.b.j("groupingExpressions", (a: any) => new GroupingExpression());
		return <GroupingExpression[]>ret_;
	}
	set g(a: GroupingExpression[]) {
		let value_ = <any>a;
		this.b.s("groupingExpressions", value_);
	}
}


