import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcFilteringExpressionsTree } from "./igc-filtering-expressions-tree";
import { IgcCancelableEventArgsDetail } from "./igc-cancelable-event-args-detail";
import { FilteringEventArgsDetail as FilteringEventArgsDetail_internal } from "./FilteringEventArgsDetail";

export class IgcFilteringEventArgsDetail extends IgcCancelableEventArgsDetail
{

protected createImplementation() : FilteringEventArgsDetail_internal
{
	return new FilteringEventArgsDetail_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : FilteringEventArgsDetail_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	get filteringExpressions() : IgcFilteringExpressionsTree {
	                                        const r = this.i.g;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
	                                        if (!(r as any).externalObject) {
	                                            let e = new IgcFilteringExpressionsTree();
	                                            if ((r as any).$type) {
	                                                (e as any)._implementation = r;
	                                            } else {
	                                                if ((e.i as any).setNativeElement) {
	                                                    (e.i as any).setNativeElement(r);
	                                                }
	                                            }
	                                            (r as any).externalObject = e;
	                                        }
		return (r as any).externalObject;
	}
	set filteringExpressions(v: IgcFilteringExpressionsTree) {
		v == null ? this.i.g = null : this.i.g = v.i;
	}
	
	    public findByName(name: string): any {
	        
	    var baseResult = super.findByName(name);
	    if (baseResult)
	    {
	        return baseResult;
	    }
	
	if (this.filteringExpressions && (this.filteringExpressions as any).name && (this.filteringExpressions as any).name == name) { return this.filteringExpressions; }
	
	        return null;
	    }
}
