import { CancelableEventArgsDetail } from "./CancelableEventArgsDetail";
import { WCNativeHelper } from "igniteui-webinputs/WCNativeHelper";
import { Base, Type, markType } from "igniteui-core/type";

/**
 * @hidden 
 */
export class PageCancellableEventArgsDetail extends CancelableEventArgsDetail {
	static $t: Type = markType(PageCancellableEventArgsDetail, 'PageCancellableEventArgsDetail', (<any>CancelableEventArgsDetail).$);
	get g(): number {
		let ret_ = this.b.k("current");
		return <number>ret_;
	}
	set g(a: number) {
		let value_ = <any>a;
		this.b.s("current", value_);
	}
	get h(): number {
		let ret_ = this.b.k("next");
		return <number>ret_;
	}
	set h(a: number) {
		let value_ = <any>a;
		this.b.s("next", value_);
	}
}


