import { Base, Type, markType } from "igniteui-core/type";
import { GridSelectionRangeDetail } from "./GridSelectionRangeDetail";

/**
 * @hidden 
 */
export class GridSelectionRangeEventArgs extends Base {
	static $t: Type = markType(GridSelectionRangeEventArgs, 'GridSelectionRangeEventArgs');
	a: GridSelectionRangeDetail = null;
	static b(a: any): GridSelectionRangeEventArgs {
		return ((() => {
			let $ret = new GridSelectionRangeEventArgs();
			$ret.a = <GridSelectionRangeDetail>a;
			return $ret;
		})());
	}
}


