import { Base, Type, markType } from "igniteui-core/type";
import { GridClipboardEventDetail } from "./GridClipboardEventDetail";

/**
 * @hidden 
 */
export class GridClipboardEventEventArgs extends Base {
	static $t: Type = markType(GridClipboardEventEventArgs, 'GridClipboardEventEventArgs');
	a: GridClipboardEventDetail = null;
	static b(a: any): GridClipboardEventEventArgs {
		return ((() => {
			let $ret = new GridClipboardEventEventArgs();
			$ret.a = <GridClipboardEventDetail>a;
			return $ret;
		})());
	}
}


