import { Base, Type, markType } from "igniteui-core/type";
import { RowExportingEventArgsDetail } from "./RowExportingEventArgsDetail";

/**
 * @hidden 
 */
export class RowExportingEventArgs extends Base {
	static $t: Type = markType(RowExportingEventArgs, 'RowExportingEventArgs');
	b: RowExportingEventArgsDetail = null;
	static a(a: any): RowExportingEventArgs {
		return ((() => {
			let $ret = new RowExportingEventArgs();
			$ret.b = <RowExportingEventArgsDetail>a;
			return $ret;
		})());
	}
}


