import { Base, toNullable, Type, markType } from "./type";
import { Color } from "./Color";

/**
 * @hidden 
 */
export class GradientStop extends Base {
	static $t: Type = markType(GradientStop, 'GradientStop');
	constructor() {
		super();
		this.offset = 0;
	}
	offset: number = 0;
	clone(): GradientStop {
		let a = new GradientStop();
		a.offset = this.offset;
		a._fill = this._fill;
		return a;
	}
	_fill: string = null;
	get fill(): string {
		return this._fill;
	}
	set fill(a: string) {
		this._fill = a;
	}
	private d: string = null;
	private g: Color = new Color();
	get color(): Color {
		if (this._fill == this.d) {
			return this.g;
		}
		let a = new Color();
		if (this._fill != null) {
			a.colorString = this._fill;
			this.g = a;
			this.d = this._fill;
		}
		return a;
	}
	set color(a: Color) {
		this.g = a;
		this.d = this.g.colorString;
		this._fill = this.d;
	}
	equals(a: any): boolean {
		if (a == null) {
			return false;
		}
		let b = <GradientStop>a;
		return this.offset == b.offset && this.color.equals(b.color) && Base.equalsStatic(this._fill, b._fill);
	}
	getHashCode(): number {
		let a: number = (this.offset);
		if (Color.e(toNullable<Color>((<any>Color).$, this.g), toNullable<Color>((<any>Color).$, null))) {
			a ^= this.g.getHashCode();
		}
		return a;
	}
}


