import { Base, Type, markType } from "igniteui-core/type";
import { FilteringEventArgsDetail } from "./FilteringEventArgsDetail";

/**
 * @hidden 
 */
export class FilteringEventArgs extends Base {
	static $t: Type = markType(FilteringEventArgs, 'FilteringEventArgs');
	b: FilteringEventArgsDetail = null;
	static a(a: any): FilteringEventArgs {
		return ((() => {
			let $ret = new FilteringEventArgs();
			$ret.b = <FilteringEventArgsDetail>a;
			return $ret;
		})());
	}
}


