import { IgcBaseEventArgsDetail } from "./igc-base-event-args-detail";
import { PageEventArgsDetail as PageEventArgsDetail_internal } from "./PageEventArgsDetail";

export class IgcPageEventArgsDetail extends IgcBaseEventArgsDetail
{

protected createImplementation() : PageEventArgsDetail_internal
{
	return new PageEventArgsDetail_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : PageEventArgsDetail_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	get previous() : number {
		return (this.i.h as number);
	}
	set previous(v: number) {
		this.i.h = +v;
	}
	get current() : number {
		return (this.i.g as number);
	}
	set current(v: number) {
		this.i.g = +v;
	}
}
