import { Base, Type, markType } from "igniteui-core/type";
import { PageEventArgsDetail } from "./PageEventArgsDetail";

/**
 * @hidden 
 */
export class PageEventArgs extends Base {
	static $t: Type = markType(PageEventArgs, 'PageEventArgs');
	b: PageEventArgsDetail = null;
	static a(a: any): PageEventArgs {
		return ((() => {
			let $ret = new PageEventArgs();
			$ret.b = <PageEventArgsDetail>a;
			return $ret;
		})());
	}
}


