import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcBaseEventArgsDetail } from "./igc-base-event-args-detail";
import { GridScrollEventArgsDetail as GridScrollEventArgsDetail_internal } from "./GridScrollEventArgsDetail";

/**
 * Event emitted when a grid is scrolled.
*/
export class IgcGridScrollEventArgsDetail extends IgcBaseEventArgsDetail
{

protected createImplementation() : GridScrollEventArgsDetail_internal
{
	return new GridScrollEventArgsDetail_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : GridScrollEventArgsDetail_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	/**
	 * The scroll direction - vertical or horizontal.
	*/
	get direction() : string {
		return (this.i.h as string);
	}
	set direction(v: string) {
		this.i.h = v;
	}
	/**
	 * The new scroll position
	*/
	get scrollPosition() : number {
		return (this.i.g as number);
	}
	set scrollPosition(v: number) {
		this.i.g = +v;
	}
}
