import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcCellType } from "./igc-cell-type";
import { CellTemplateContext as CellTemplateContext_internal } from "./CellTemplateContext";

export class IgcCellTemplateContext
{

protected createImplementation() : CellTemplateContext_internal
{
	return new CellTemplateContext_internal();
}
	protected _implementation: any;
	                            /**
	                             * @hidden 
	                             */
	public get i() : CellTemplateContext_internal {
		return this._implementation;
	}
	private onImplementationCreated() {
		
	}
	constructor() {
	this._implementation = this.createImplementation();
	(<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	}
	protected _provideImplementation(i: any) {
	    this._implementation = i;
	    (<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	}
	get implicit() : any {
		return this.i.f as any;
	}
	set implicit(v: any) {
	                                        this.i.f = v;
	}
	get additionalTemplateContext() : any {
		return this.i.e as any;
	}
	set additionalTemplateContext(v: any) {
	                                        this.i.e = v;
	}
	get cell() : IgcCellType {
	                                        const r = this.i.a;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
	                                        if (!(r as any).externalObject) {
	                                            let e = new IgcCellType();
	                                            if ((r as any).$type) {
	                                                (e as any)._implementation = r;
	                                            } else {
	                                                if ((e.i as any).setNativeElement) {
	                                                    (e.i as any).setNativeElement(r);
	                                                }
	                                            }
	                                            (r as any).externalObject = e;
	                                        }
		return (r as any).externalObject;
	}
	set cell(v: IgcCellType) {
		v == null ? this.i.a = null : this.i.a = v.i;
	}
	
	    public findByName(name: string): any {
	        
	    if ((this as any).findEphemera) {
	        if (name && name.indexOf("@@e:") == 0) {
	            return (this as any).findEphemera(name);
	        }
	    }
	
	if (this.cell && (this.cell as any).name && (this.cell as any).name == name) { return this.cell; }
	
	        return null;
	    }
	public setNativeElement(element: any) {
		this.i.setNativeElement(element);
	}
}
