import { Base, Type, markType } from "./type";

/**
 * @hidden 
 */
export class RectDescription extends Base {
	static $t: Type = markType(RectDescription, 'RectDescription');
	private _left: number = 0;
	get left(): number {
		return this._left;
	}
	set left(a: number) {
		this._left = a;
	}
	private _top: number = 0;
	get top(): number {
		return this._top;
	}
	set top(a: number) {
		this._top = a;
	}
	private _width: number = 0;
	get width(): number {
		return this._width;
	}
	set width(a: number) {
		this._width = a;
	}
	private _height: number = 0;
	get height(): number {
		return this._height;
	}
	set height(a: number) {
		this._height = a;
	}
}


