import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcRowType } from "./igc-row-type";
import { IgcCancelableEventArgsDetail } from "./igc-cancelable-event-args-detail";
import { PinRowEventArgsDetail as PinRowEventArgsDetail_internal } from "./PinRowEventArgsDetail";
import { ensureBool } from "igniteui-core/componentUtil";

/**
 * Event emitted when a row's pin state changes.
*/
export class IgcPinRowEventArgsDetail extends IgcCancelableEventArgsDetail
{

protected createImplementation() : PinRowEventArgsDetail_internal
{
	return new PinRowEventArgsDetail_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : PinRowEventArgsDetail_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	/**
	 * The ID of the row, that was pinned/unpinned.
	 * ID is either the primaryKey value or the data record instance.
	*/
	get rowID() : any {
		return this.i.j as any;
	}
	set rowID(v: any) {
	                                        this.i.j = v;
	}
	get row() : IgcRowType {
	                                        const r = this.i.g;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
	                                        if (!(r as any).externalObject) {
	                                            let e = new IgcRowType();
	                                            if ((r as any).$type) {
	                                                (e as any)._implementation = r;
	                                            } else {
	                                                if ((e.i as any).setNativeElement) {
	                                                    (e.i as any).setNativeElement(r);
	                                                }
	                                            }
	                                            (r as any).externalObject = e;
	                                        }
		return (r as any).externalObject;
	}
	set row(v: IgcRowType) {
		v == null ? this.i.g = null : this.i.g = v.i;
	}
	/**
	 * The index at which to pin the row in the pinned rows collection.
	*/
	get insertAtIndex() : number {
		return (this.i.i as number);
	}
	set insertAtIndex(v: number) {
		this.i.i = +v;
	}
	/**
	 * Whether or not the row is pinned or unpinned.
	*/
	get isPinned() : boolean {
		return (this.i.h as boolean);
	}
	set isPinned(v: boolean) {
		this.i.h = ensureBool(v);
	}
	
	    public findByName(name: string): any {
	        
	    var baseResult = super.findByName(name);
	    if (baseResult)
	    {
	        return baseResult;
	    }
	
	if (this.row && (this.row as any).name && (this.row as any).name == name) { return this.row; }
	
	        return null;
	    }
}
