import { Base, Type, markType } from "igniteui-core/type";
import { ColumnSelectionEventArgsDetail } from "./ColumnSelectionEventArgsDetail";

/**
 * @hidden 
 */
export class ColumnSelectionEventArgs extends Base {
	static $t: Type = markType(ColumnSelectionEventArgs, 'ColumnSelectionEventArgs');
	b: ColumnSelectionEventArgsDetail = null;
	static a(a: any): ColumnSelectionEventArgs {
		return ((() => {
			let $ret = new ColumnSelectionEventArgs();
			$ret.b = <ColumnSelectionEventArgsDetail>a;
			return $ret;
		})());
	}
}


