import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcBaseEventArgsDetail } from "./igc-base-event-args-detail";
import { ColumnVisibilityChangedEventArgsDetail as ColumnVisibilityChangedEventArgsDetail_internal } from "./ColumnVisibilityChangedEventArgsDetail";
import { ensureBool } from "igniteui-core/componentUtil";

export class IgcColumnVisibilityChangedEventArgsDetail extends IgcBaseEventArgsDetail
{

protected createImplementation() : ColumnVisibilityChangedEventArgsDetail_internal
{
	return new ColumnVisibilityChangedEventArgsDetail_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : ColumnVisibilityChangedEventArgsDetail_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	get column() : any {
		return this.i.h as any;
	}
	set column(v: any) {
	                                        this.i.h = v;
	}
	/**
	 * The new hidden state that the column will have, if operation is succesfull.
	 * Will be `true` when hiding and `false` when showing.
	*/
	get newValue() : boolean {
		return (this.i.g as boolean);
	}
	set newValue(v: boolean) {
		this.i.g = ensureBool(v);
	}
}
