import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcColumn } from "./igc-column";
import { IgcBaseEventArgsDetail } from "./igc-base-event-args-detail";
import { PinColumnEventArgsDetail as PinColumnEventArgsDetail_internal } from "./PinColumnEventArgsDetail";
import { ensureBool } from "igniteui-core/componentUtil";

/**
 * The event arguments after a column's pin state is changed.
 * `insertAtIndex`specifies at which index in the pinned/unpinned area the column was inserted.
 * `isPinned` returns the actual pin state of the column after the operation completed.
*/
export class IgcPinColumnEventArgsDetail extends IgcBaseEventArgsDetail
{

protected createImplementation() : PinColumnEventArgsDetail_internal
{
	return new PinColumnEventArgsDetail_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : PinColumnEventArgsDetail_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	get column() : IgcColumn {
	                                        const r = this.i.g;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
	                                        if (!(r as any).externalObject) {
	                                            let e = new IgcColumn();
	                                            if ((r as any).$type) {
	                                                (e as any)._implementation = r;
	                                            } else {
	                                                if ((e.i as any).setNativeElement) {
	                                                    (e.i as any).setNativeElement(r);
	                                                }
	                                            }
	                                            (r as any).externalObject = e;
	                                        }
		return (r as any).externalObject;
	}
	set column(v: IgcColumn) {
		v == null ? this.i.g = null : this.i.g = v.i;
	}
	/**
	 * If pinned, specifies at which index in the pinned area the column is inserted.
	 * If unpinned, specifies at which index in the unpinned area the column is inserted.
	*/
	get insertAtIndex() : number {
		return (this.i.i as number);
	}
	set insertAtIndex(v: number) {
		this.i.i = +v;
	}
	/**
	 * Returns the actual pin state of the column.
	 * If pinning/unpinning is succesfull, value of `isPinned` will change accordingly when read in the "-ing" and "-ed" event.
	*/
	get isPinned() : boolean {
		return (this.i.h as boolean);
	}
	set isPinned(v: boolean) {
		this.i.h = ensureBool(v);
	}
	
	    public findByName(name: string): any {
	        
	    var baseResult = super.findByName(name);
	    if (baseResult)
	    {
	        return baseResult;
	    }
	
	if (this.column && (this.column as any).name && (this.column as any).name == name) { return this.column; }
	
	        return null;
	    }
}
