import { Base, Type, markType } from "igniteui-core/type";
import { SortingExpression } from "./SortingExpression";

/**
 * @hidden 
 */
export class SortingExpressionEventArgs extends Base {
	static $t: Type = markType(SortingExpressionEventArgs, 'SortingExpressionEventArgs');
	a: SortingExpression[] = null;
	static b(a: any): SortingExpressionEventArgs {
		return ((() => {
			let $ret = new SortingExpressionEventArgs();
			$ret.a = <SortingExpression[]>a;
			return $ret;
		})());
	}
}


