import { Base, Type, markType } from "./type";
import { TransactionType } from "./TransactionType";

/**
 * @hidden 
 */
export class Transaction extends Base {
	static $t: Type = markType(Transaction, 'Transaction');
	private _id: any = null;
	get id(): any {
		return this._id;
	}
	set id(a: any) {
		this._id = a;
	}
	private _transactionID: number = 0;
	get transactionID(): number {
		return this._transactionID;
	}
	set transactionID(a: number) {
		this._transactionID = a;
	}
	private _type: TransactionType = <TransactionType>0;
	get type(): TransactionType {
		return this._type;
	}
	set type(a: TransactionType) {
		this._type = a;
	}
	private _value: any = null;
	get value(): any {
		return this._value;
	}
	set value(a: any) {
		this._value = a;
	}
	private _error: string = null;
	get error(): string {
		return this._error;
	}
	set error(a: string) {
		this._error = a;
	}
}


