import { Base, Type, markType } from "igniteui-core/type";
import { BaseEventArgsDetail } from "./BaseEventArgsDetail";

/**
 * @hidden 
 */
export class BaseEventArgs extends Base {
	static $t: Type = markType(BaseEventArgs, 'BaseEventArgs');
	b: BaseEventArgsDetail = null;
	static a(a: any): BaseEventArgs {
		return ((() => {
			let $ret = new BaseEventArgs();
			$ret.b = <BaseEventArgsDetail>a;
			return $ret;
		})());
	}
}


