import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { ColumnPinningPosition, ColumnPinningPosition_$type } from "./ColumnPinningPosition";
import { RowPinningPosition, RowPinningPosition_$type } from "./RowPinningPosition";
import { PinningConfig as PinningConfig_internal } from "./PinningConfig";
import { ensureEnum } from "igniteui-core/componentUtil";

/**
 * An interface describing settings for row/column pinning position.
*/
export class IgcPinningConfig
{

protected createImplementation() : PinningConfig_internal
{
	return new PinningConfig_internal();
}
	protected _implementation: any;
	                            /**
	                             * @hidden 
	                             */
	public get i() : PinningConfig_internal {
		return this._implementation;
	}
	private onImplementationCreated() {
		
	}
	constructor() {
	this._implementation = this.createImplementation();
	(<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	}
	protected _provideImplementation(i: any) {
	    this._implementation = i;
	    (<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	}
	get columns() : ColumnPinningPosition {
		return this.i.a;
	}
	set columns(v: ColumnPinningPosition) {
		this.i.a = ensureEnum<ColumnPinningPosition>(ColumnPinningPosition_$type, v);
	}
	get rows() : RowPinningPosition {
		return this.i.b;
	}
	set rows(v: RowPinningPosition) {
		this.i.b = ensureEnum<RowPinningPosition>(RowPinningPosition_$type, v);
	}
	
	    public findByName(name: string): any {
	        
	    if ((this as any).findEphemera) {
	        if (name && name.indexOf("@@e:") == 0) {
	            return (this as any).findEphemera(name);
	        }
	    }
	
	
	        return null;
	    }
	public setNativeElement(element: any) {
		this.i.setNativeElement(element);
	}
}
