import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { CancelableEventArgsDetail as CancelableEventArgsDetail_internal } from "./CancelableEventArgsDetail";
import { ensureBool } from "igniteui-core/componentUtil";

export class IgcCancelableEventArgsDetail
{

protected createImplementation() : CancelableEventArgsDetail_internal
{
	return new CancelableEventArgsDetail_internal();
}
	protected _implementation: any;
	                            /**
	                             * @hidden 
	                             */
	public get i() : CancelableEventArgsDetail_internal {
		return this._implementation;
	}
	private onImplementationCreated() {
		
	}
	constructor() {
	this._implementation = this.createImplementation();
	(<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	}
	protected _provideImplementation(i: any) {
	    this._implementation = i;
	    (<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	}
	/**
	 * Provides the ability to cancel the event.
	*/
	get cancel() : boolean {
		return (this.i.c as boolean);
	}
	set cancel(v: boolean) {
		this.i.c = ensureBool(v);
	}
	
	    public findByName(name: string): any {
	        
	    if ((this as any).findEphemera) {
	        if (name && name.indexOf("@@e:") == 0) {
	            return (this as any).findEphemera(name);
	        }
	    }
	
	
	        return null;
	    }
	public setNativeElement(element: any) {
		this.i.setNativeElement(element);
	}
}
